#root,
html,
body {
  margin: 0;
  padding: 0;
  height: 100vh;
  box-sizing: border-box;
}

.app {
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url("https://picsum.photos/1920/1080?random=1");
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.card {
  background: url("https://image.freepik.com/free-photo/design-space-paper-textured-background_53876-42312.jpg");
  width: 60%;
  height: 55%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 4%;
  padding: 3%;
  box-shadow: 0 0 10px black;
}

.heading {
  display: flex;
  align-items: center;
  position: relative;
  font-size: 27px;
  visibility: hidden;
  font-family: monospace;
  color: "#383428";
  margin-top: 10px;
}

@media only screen and (max-width: 970px) {
  .card {
    width: 70%;
    height: 45%;
  }
  .heading {
    font-size: 24px;
  }
}

@media only screen and (max-width: 475px) {
  .card {
    width: 95%;
    height: 55%;
  }
  .heading {
    font-size: 23px;
  }
}
@media only screen and (max-width: 599px) {
  .card {
    width: 95%;
    height: 55%;
  }
  .heading {
    font-size: 23px;
  }
}
